/* Standard List Styles */
.list-panel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 300px;
  min-width: 300px;
  margin: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.list-item {
  height: 50px;
  margin: 1px 0;
  padding: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
}

.list-item:hover {
  background-color: #e2e2e2;
  border-radius: 2px;
}

.selected-item {
  background-color: #adadadf2;
}

/* Badge List Styles */
.badge-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 10px 0 0;
}

.badge {
  display: -ms-flexbox;
  display: flex;
  width: 175px;
  height: 50px;
  margin: 5px;
  border-radius: 2px;
  background-color: #d3d3d3;
  -webkit-box-shadow: 0px 0px 2px 1px rgba(0,0,0,0.34);
          box-shadow: 0px 0px 2px 1px rgba(0,0,0,0.34);
}

.badge-image {
  height: 44px;
  width: 44px;
  padding: 3px;
  border-radius: 25px;
}


